"use client";

import { Moon, Sun, Monitor } from "lucide-react";
import { useTheme } from "./ThemeProvider";
import { motion, AnimatePresence } from "framer-motion";
import { useState } from "react";

export function ThemeToggle() {
  const { theme, setTheme, resolvedTheme } = useTheme();
  const [isOpen, setIsOpen] = useState(false);

  const themes = [
    { value: "light", label: "Light", icon: Sun },
    { value: "dark", label: "Dark", icon: Moon },
    { value: "system", label: "System", icon: Monitor },
  ] as const;

  const currentTheme = themes.find(t => t.value === theme) || themes[0];
  const CurrentIcon = currentTheme.icon;

  const handleThemeChange = (newTheme: "light" | "dark" | "system") => {
    setTheme(newTheme);
    setIsOpen(false);
  };

  return (
    <div className="relative">
      {/* Toggle Button */}
      <motion.button
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
        onClick={() => setIsOpen(!isOpen)}
        className={`
          p-2 rounded-lg transition-all duration-300 border-2
          ${resolvedTheme === 'dark' 
            ? 'bg-gray-800 hover:bg-gray-700 border-gray-600 text-gray-100' 
            : 'bg-white hover:bg-gray-50 border-gray-200 text-gray-900'
          }
          shadow-lg hover:shadow-xl
          focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2
          ${resolvedTheme === 'dark' ? 'focus:ring-offset-gray-800' : 'focus:ring-offset-white'}
        `}
        aria-label="Toggle theme"
        aria-expanded={isOpen}
      >
        <CurrentIcon className="w-5 h-5" />
      </motion.button>

      {/* Dropdown Menu */}
      <AnimatePresence>
        {isOpen && (
          <>
            {/* Backdrop */}
            <div 
              className="fixed inset-0 z-40" 
              onClick={() => setIsOpen(false)}
            />
            
            {/* Menu */}
            <motion.div
              initial={{ opacity: 0, scale: 0.95, y: -10 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.95, y: -10 }}
              transition={{ duration: 0.2 }}
              className={`
                absolute right-0 top-full mt-2 z-50
                min-w-[140px] rounded-lg shadow-xl border
                ${resolvedTheme === 'dark' 
                  ? 'bg-gray-800 border-gray-600' 
                  : 'bg-white border-gray-200'
                }
                overflow-hidden
              `}
            >
              {themes.map((themeOption) => {
                const Icon = themeOption.icon;
                const isSelected = theme === themeOption.value;
                
                return (
                  <motion.button
                    key={themeOption.value}
                    whileHover={{ backgroundColor: resolvedTheme === 'dark' ? '#374151' : '#f9fafb' }}
                    onClick={() => handleThemeChange(themeOption.value)}
                    className={`
                      w-full px-4 py-3 text-left flex items-center gap-3
                      transition-colors duration-200
                      ${resolvedTheme === 'dark' 
                        ? 'text-gray-100 hover:bg-gray-700' 
                        : 'text-gray-900 hover:bg-gray-50'
                      }
                      ${isSelected 
                        ? resolvedTheme === 'dark' 
                          ? 'bg-blue-900/30 text-blue-300' 
                          : 'bg-blue-50 text-blue-700'
                        : ''
                      }
                      focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-inset
                    `}
                  >
                    <Icon className="w-4 h-4" />
                    <span className="text-sm font-medium">{themeOption.label}</span>
                    {isSelected && (
                      <motion.div
                        initial={{ scale: 0 }}
                        animate={{ scale: 1 }}
                        className={`
                          ml-auto w-2 h-2 rounded-full
                          ${resolvedTheme === 'dark' ? 'bg-blue-400' : 'bg-blue-600'}
                        `}
                      />
                    )}
                  </motion.button>
                );
              })}
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </div>
  );
}
